.flipCard {
  background-color: transparent;
  width: 300px;
  height: 200px;
  border: 1px solid #f1f1f1;
  perspective: 1000px;
  border-radius: 3px;
}
.flipCard-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.9s;
  transform-style: preserve-3d;
}
.flipCard:hover .flipCard-inner {
  transform: rotateY(180deg);
}

.flipCard-front,
.flipCard-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden; /* Safari */
  backface-visibility: hidden;
  border-radius: 6px;
}
.flipCard-front {
  background-color: rgb(245, 190, 9);
  color: black;
  border-radius: 6px;
}
.flipCard-back {
  /* background-color: rgb(245, 190, 9); */
  color: black;
  transform: rotateY(180deg);
  border-radius: 6px;
}
